@import "intro";
@import "heroCreator";

//reset
*{
   margin: 0; padding: 0;
 }

html, body {
   width: 100%;
   height: 100%;
 }

 header, footer{
   height: 10vh;
 }

 #mainPart{
     height: 80vh;
 }

//ustawienie koloru tła dla pierwszego intra gry
body{
   background-color: #D3D3D3;
 }

//ustawienie wysokoci i szerokości dla poszczególnych części strony (gry)
header, footer{
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
}

#version{
  font-family: 'Merienda One', cursive;
  text-align: center;
}

#cssValidation{
  display: none;
}

#mainDescription{
	display: flex;
	flex-wrap: wrap;
  justify-content: center;
	align-content: flex-start;
}

.overflowScroll60vh{
	overflow: auto;
	height: 67vh;
}

p.heroRandom{
    width: 144px;
    height: 30px;
    text-align: center;
  }
